@use 'reset';
@use 'font-face';

:root {
    --font-stack-body: 'acherus_grotesque', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', 'PingFang TC', sans-serif;
    --font-stack-heading: 'mixta_pro', Georgia, 'Times New Roman', Times, 'SongTi TC', serif;
    --font-stack-accent: 'cartograph_cf', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    --font-stack-accent2: 'strange_times';  // Needs to be used at least once here or it won't be included in the output

    --color-1: #FFF8E4;
    --color-2: #272A45;
    --color-3: #D19245;
}

* {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

* + * {
    margin-top: 1em;
}

* + br {
    margin-top: 0;
}

html {
    background: var(--color-1);
    color: var(--color-2);
    font-size: 16px;
    font-family: var(--font-stack-body);
    font-feature-settings: 'onum' 1, 'case' 1;
    /* Old-style numerals and case-sensitive punctuation */

    position: relative;
    height: 100%;
}

footer {
    height: 100%;
    position: relative;
    grid-area: footer;
    margin-top: 5em;

    font-size: 0.8em;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 0.1em;
    color: var(--color-3);
    text-align: center;

    section {
        margin-left: 2em;
        margin-right: 2em;
    }

    section + section {
        margin-top: 5em;
    }

    p + p {
        margin-top: 0;
    }

    a {
        text-decoration: none;
    }
}

body {
    margin: 0;

    display: grid;
    grid-template-areas:
    "header"
    "main"
    "footer";
    grid-template-rows: auto 1fr auto;
}

header {
    text-align: center;
    grid-area: header;
    margin-top: 10em;
}

h1 {
    text-transform: lowercase;
    font-size: 5em;
    margin-top: 0;
    margin-bottom: 0.1em;
    position: relative;

    font-family: var(--font-stack-heading);
}

a {
    color: inherit;
}

[target="_blank"]:after {
    content: ' ↗';
    font-size: 0.8em;
 }

nav a {
    text-decoration: none;
}

a:hover, a:focus, a:active {
    color: var(--color-3);
    text-decoration: underline;
}

.subheading {
    font-family: var(--font-stack-accent);
    font-weight: 200;
    font-style: italic;
    text-transform: lowercase;
    margin-top: -0.5em;
}

main {
    margin-top: 1em;
    max-width: 35em;
    margin-left: auto;
    margin-right: auto;
    padding-left: 2em;
    padding-right: 2em;
    grid-area: main;

    font-size: 0.8em;
}

section + section {
    margin-top: 4em;
}

p {
    line-height: 1.8;
}

* + p {
    margin-top: 1em;
}

h2 {
    font-family: var(--font-stack-heading);
    font-weight: 400;
    font-style: italic;
    font-size: 4em;
    text-transform: lowercase;
    text-align: center;
    margin-bottom: 0.8em;
}

li + li {
    margin-top: 0;
}

.projects-list {
    list-style-type: none;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
}

#design-projects {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 4em;
    column-gap: 4em;
}

#design-projects figure {
    margin: 0;
    position: relative;
}

#design-projects figcaption {
    font-size: 0.8em;
    text-transform: uppercase;
    font-feature-settings: 'onum' 0;
    letter-spacing: 0.08em;
    color: var(--color-1);
    background-color: var(--color-2);
    padding: 1em 1.5em;

    position: absolute;
    bottom: -1em;
    left: -2em;
}

#design-projects img {
    max-width: 12em;
}

#design-projects {
    a:hover, a:focus {
        figcaption {
            background-color: var(--color-3);
        }
        img {
            transform: scale(1.025);
        }
    }
}

#translation-projects {
    line-height: 1.8;
    text-align: center;
}

#translation-projects li {
    display: inline;
    // padding-right: 0;
}

#translation-projects {
    li + li::before {
            display: inline-block;
            margin: 0.5em 0.5em 0 0.3em;
            width: 0.7em;
            height: 0.7em;
            border-radius: 50%;
            content: "";
            background-color: var(--color-3);
    }
}

#translation-projects a {
    text-decoration: none;
}

#translation-projects a:hover {
    text-decoration: underline;
}

@media (min-width: 600px) {
    html {
        font-size: 20px;
    }
    h1 {
        font-size: 6em;
    }
    main {
        margin-top: 3em;
    }
    footer {
        font-size: 0.6em;
        margin-top: 10em;
    }
}

@media (min-width: 800px) {
    #footer-credits {
        p {
            display: inline-block;
        }
        p + p::before {
            display: inline-block;
            content: "·";
            margin: 0 0.5ch;
        }
    }
}